<template>
  <div id="app">
    <div class="layout">
      <Nav></Nav>
      <left-nav></left-nav>  
    </div>
</div>
</template>

<script>
import Nav from "./components/Nav";
import LeftNav from "./components/LeftNav"

export default {
  name: "app",
  components:{
    Nav,
    LeftNav
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 14px;
}

</style>



