<template>
  <div class="godz-nav">
      <Menu mode="horizontal" theme="dark" active-name="1">
        <div class="layout-logo">注意，这是一个logo</div>
        <div class="layout-nav">
            <MenuItem name="1">
                <Icon type="ios-navigate"></Icon>
                主页
            </MenuItem>
            <MenuItem name="2">
                <Icon type="ios-keypad"></Icon>
                类别
            </MenuItem>
            <MenuItem name="3">
                <Icon type="ios-analytics"></Icon>
                相册
            </MenuItem>
            <MenuItem name="4">
                <Icon type="ios-paper"></Icon>
                关于
            </MenuItem>
        </div>
      </Menu>
  </div>
</template>
<script>
export default {
  name : "godz-nav"
}
</script>
<style>
.layout-logo{
    font-size: 12px;
    width: 150px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
    color: #fff;
    line-height: 30px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
}
</style>




